export default {
	data() {
		return {
			num:"",
			userData:"",
			noofoneneedNum:"",
			availNum:""
		}
	},
	onLoad() {
		this.getUserInfo();
		this.availableqty();
		this.noofoneneed();
	},
	methods: {
		getUserInfo(){
			const that = this;
			that.$api.userinfoQuery({}).then(res => {
				if (res.success) {
					let result =res.datas;
					result.headImgUrl = result.headImgUrl?result.headImgUrl:'../../../../static/images/defaultUserPic.png';
					result.nickName=result.nickName?result.nickName:result.mobile;
					that.userData=result;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		availableqty(){
			const that = this;
			that.$api.availableqty({}).then(res => {
				if (res.success) {
					that.availNum=res.datas;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		noofoneneed(){
			const that = this;
			that.$api.noofoneneed({}).then(res => {
				if (res.success) {
					let result =res.datas;
					that.noofoneneedNum=result;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		sub(){
			const that = this;
			let numText=/^[0-9]*[1-9][0-9]*$/;
			console.log(that.num);
			if(!numText.test(that.num)){
				uni.showToast({
					title:"请输入整数",
					icon:"none"
				})
			}
			else{
				that.$api.quotaexchange({
					qty:that.num
				}).then(res => {
					if (res.success) {
						uni.showToast({
							title:"兑换成功",
						})
						setTimeout(()=>{
							that.num="";
							that.availableqty();	
						},1000)
					} else {
						uni.showToast({
							title: res.message,
							icon: "none"
						})
					}
				})
			}
		},
		gotoDetailed(){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/exchange/detailed"
			})
		}
	}
}
